* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 0 10px;
  background-color: #f5f5f5;
}

.bar-container-parent {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .bar-container {
    width: 47%;
    height: 90vh;
    display: flex;
    align-items: flex-end;
    border: 5px solid #555;
    overflow: hidden;
    transition: all 0.2s ease-in-out;
    position: relative;
    cursor: pointer;

    .sort-name {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 2.5rem;
      font-weight: bold;
      color: #555;
      z-index: 100;
    }

    &:nth-child(3) {
      .bar {
        background-color: rgb(245, 152, 162);

        &:hover {
          background-color: #a23;
        }
      }
    }

    .bar {
      width: 1%;
      background-color: #add8ff;
      position: relative;
      border-width: 0 1px 0 0;

      cursor: pointer;

      &.red {
        background-color: #ff4455;
      }

      &.yellow {
        background-color: #ff3;
      }

      &.green {
        background-color: green;
      }

      &.pink {
        // background-color: rgb(22, 247, 22);
        background-color: #fefe42;
      }

      &.lightblue {
        background-color: #4242fe;
      }

      &::before {
        content: attr(data-value);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        border: 1px solid #000;
        background-color: white;
        border-radius: 10%;
        z-index: 10;
        font-size: 1.5rem;
        padding: 5px;
        display: none;
        font-family: sans-serif;
        transition: display 0.2s ease-in-out;
      }

      &:hover {
        background-color: #08a;
        border: 1px solid #333;

        &::before {
          display: block;
        }
      }
    }

    &.selected {
      border: 5px solid green;
      box-shadow: 0 0 6px rgba($color: #000000, $alpha: 0.6);
      transform: scale(1.01);

      .bar {
        border: none;
      }
    }
  }

  .start-button {
    padding: 2px 5px;
    font-size: 1rem;
    cursor: pointer;
    border: 2px solid #000;
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
    margin: 0 0.5rem;

    &:hover:not(:disabled) {
      background-color: #000;
      color: #fff;
    }
  }

  @media (max-width: 1200px) {
    flex-direction: column;

    .bar-container {
      width: calc(100% - 1rem);
      height: 40vh;

      .sort-name {
        font-size: clamp(1.2rem, 3vw, 2.2rem);
      }
    }

    .start-button {
      margin: clamp(0.5rem, 1.5vw, 1rem) 0;
      padding: 0.5rem;
      font-size: clamp(0.5rem, 2vw, 1rem);
    }
  }
}

.buttons-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.5rem 0;
  flex-wrap: wrap;
  gap: 20px;

  button {
    margin: 0 10px;
    padding: 10px 20px;
    cursor: pointer;
    box-shadow: 0 0 2px rgba($color: #000000, $alpha: 0.6);
    border: 2px solid #000;
    border-radius: 5px;
  }

  input,
  label {
    position: relative;
    margin: 0 10px;
  }

  input::before {
    content: attr(value);
    position: absolute;
    left: 50%;
    top: -30px;
    padding: 5px;
    background-color: white;
    border: 1px solid #000;
    border-radius: 10%;
  }

  @media (max-width: 1240px) {
    .sliders {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      > input {
        width: 100%;
      }
    }

    .buttons {
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      gap: 20px;
      flex-wrap: wrap;
    }
  }
}

.result {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
  min-height: 100vh;
  padding: 0 2rem;

  .result-info {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 2rem;
    margin-top: 4rem;

    > h1 {
      span {
        color: red;
        text-shadow: 0 0 2px #000;
      }
    }

    #restart {
      margin: 20px 0;
      padding: 15px 30px;
      cursor: pointer;
      font-size: 1.5rem;
      border: 2px solid #000;
      transition: all 0.2s ease-in-out;

      &:hover {
        background-color: #000;
        color: #fff;
      }
    }

    .sort-info {
      margin: 2rem 0;

      table {
        border-collapse: collapse;
        border: 1px solid #000;
        width: 100%;
        font-size: 1.5rem;
        text-align: center;

        tr {
          min-height: 60px;
        }

        th,
        td {
          border: 1px solid #000;
          padding: 10px;
        }

        th {
          font-size: 2rem;
        }

        td:not(:nth-child(1)) {
          font-size: 1.5rem;
          color: #555;
        }
      }
    }

    @media (max-width: 767px) {
      margin: clamp(1rem, 4vw, 2rem);
      h1 {
        font-size: clamp(1rem, 4vw, 2rem);
      }
      #restart {
        font-size: 1.2rem;
        padding: 10px 20px;
      }

      .sort-info {
        table {
          tr {
            min-height: 20px;
          }

          th,
          td {
            padding: 1vw;
          }

          th {
            font-size: clamp(1rem, 4vw, 2rem);
          }

          td {
            font-size: clamp(0.7rem, 3vw, 1.2rem) !important;
          }
        }
      }
    }
  }

  .chart {
    width: 500px;
    height: 600px;
    margin: 0 2rem;

    @media (max-width: 1023px) {
      width: 80vw;
      height: 80vw;
      margin: clamp(0.5rem, 3vw, 2rem) auto;
    }
  }

  @media (max-width: 1023px) {
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    margin-top: 2rem;
    padding: 0;
  }
}
